
import axios from 'axios';
import CryptoItem from './CryptoItem.vue';

// import { ref } from 'vue';

// const open = ref(false);

export default {
  data: function () {
    return {
      coins: [],
      selectedCurrency: 'eur', // Default currency
    };
  },
  mounted() {
    setTimeout(() => {
      this.loadCoins();
    }, 1500);
  },
  methods: {
    loadCoins: function () {
      axios
        .get(this.getApiUrl())
        .then((response) => {
          this.coins = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeCurrency: function (currency) {
      this.selectedCurrency = currency;
      this.coins = [];
      setTimeout(() => {
        this.loadCoins();
      }, 1500);
    },
    getApiUrl: function () {
      return `https://api.coingecko.com/api/v3/coins/markets?vs_currency=${this.selectedCurrency}&order=market_cap_desc&per_page=10&page=1&sparkline=false&locale=en`;
    },
  },
  components: {
    CryptoItem,
  },
};
